import axios from "axios";
import React from "react";
import { Link } from "react-router-dom";
import styled from "styled-components";
import { useEffect } from "react";
import { useState } from "react";

const MoviesDh = () => {
	const [moviesDetails, setMoviesDetails] = useState([]);
	const [count, setCount] = useState(1);
	const [clicked, setClicked] = useState(false);
	const tmdb_api = "0bcafd5b459497ec53d81e9fc940f86d";

	useEffect(() => {
		count !== 4 &&
			setCount((prev) => {
				return prev + 1;
			});
		// axios
		// 	.get(
		// 		`https://api.themoviedb.org/3/discover/movie?api_key=0bcafd5b459497ec53d81e9fc940f86d&language=en-US&sort_by=popularity.desc&include_adult=false&page=${count}&with_watch_monetization_types=flatrate`
		// 	)
		// 	.then((res) => {
		// 		setMoviesDetails((prev) => {
		// 			return [...prev, ...res.data.results];
		// 		});
		// 	});
		fetch(
			`https://api.themoviedb.org/3/discover/movie?api_key=0bcafd5b459497ec53d81e9fc940f86d&language=en-US&sort_by=popularity.desc&include_adult=false&page=${count}&with_watch_monetization_types=flatrate`
		)
			.then((res) => res.json())
			.then((res) => {
				setMoviesDetails((prev) => {
					return [...prev, ...res.results];
				});
			});
	}, [count]);
	console.log(moviesDetails);
	return (
		<>
			{count === 4 && (
				<Content>
					{moviesDetails.map((ele, index) => {
						console.log(ele);
						return (
							<Link
								to={"/detail"}
								key={ele.id}
								state={{
									data: moviesDetails.filter((info) => {
										return info.id === ele.id;
									}),
								}}>
								<Wrap rating={ele.vote_average.toFixed(1)}>
									<img
										src={`https://image.tmdb.org/t/p/original/${ele.poster_path}`}
										alt={ele.original_title}
									/>
								</Wrap>
							</Link>
						);
					})}
				</Content>
			)}
		</>
	);
};

export default MoviesDh;

const Content = styled.div`
	margin: 20px 0px;
	display: grid;
	grid-template-columns: repeat(10, minmax(0, 1fr));
	grid-template-rows: repeat(auto-fit, 1fr);
	grid-gap: 25px;
	@media (max-width: 768px) {
		grid-template-columns: repeat(5, minmax(100px, 1fr));
	}
	@media (max-width: 648px) {
		grid-template-columns: repeat(4, minmax(85px, 1fr));
	}
	@media (max-width: 500px) {
		grid-template-columns: repeat(4, minmax(70px, 1fr));
	}

	@media (max-width: 376px) {
		justify-content: center;
		grid-template-columns: repeat(4, minmax(50px, 1fr));
	}
`;

const Wrap = styled.div`
	cursor: pointer;
	box-shadow: rgb(0 0 0 /69%) 0px 26px 30px -10px,
		rgb(0 0 0 /73%) 0px 16px 10px -10px;
	border: 2px solid rgba(249, 249, 249, 0.1);
	transition: all 250ms cubic-bezier(0.25, 0.46, 0.45, 0.94) 0s;
	border-radius: 10px;
	height: 100%;
	img {
		height: 100%;
		width: 100%;
		object-fit: cover;
		border-radius: 10px;
	}
	&:before {
		position: absolute;
		content: "${(props) => props.rating}";
		border-radius: 50%;
		background-color: #081c22;
		width: 35px;
		height: 35px;
		font-size: 10px;
		// text-align: center;
		color: #fff;
		display: flex;
		justify-content: center;
		align-items: center;
		// padding: 1px;
		bottom: -7px;
		right: -7px;
	}
	&:hover {
		transform: scale(1.05);
		box-shadow: rgb(0 0 0 /80%) 0px 40px 58px -16px,
			rgb(0 0 0 /72%) 0px 30px 22px -10px;
		border-color: rgba(249, 249, 249, 0.8);
	}
`;

